import { useNavigate } from 'react-router-dom';
import styles from './Category.module.css'
const Category = ({category}) => {
    const nav = useNavigate();
    let categoryInRussian = category;
    switch (categoryInRussian) {
        case 'sedan':
            categoryInRussian = '';
            break;
        case 'crossover':
            categoryInRussian = '';
            break;
        case 'coupe':
            categoryInRussian = '';
            break;
        case 'cabriolet':
            categoryInRussian = '';
            break;
        default:
            break;
    }

    return (
        <div className={styles.container}>
            <span>{categoryInRussian}</span>
            {/* <button onClick={() => nav(`/car_salon_3kurs/catalog/${categoryInEnglish}`)}> &#x2192;</button> */}
            <button onClick={() => nav(`/car_salon_3kurs/catalog/${category}`)}> &#x2192;</button>

        </div>
    )

}

export default Category;